import React from 'react'
import {useSelector, useDispatch} from 'react-redux'
import {Panel, Bar, Toolbar, Button, List, ListItem, Divider} from 'react95'

import {closeWindow, maximizeWindow} from '../reducers/windows'

const Footer = () => {
    const windows = useSelector(state => state.windows.items)
    const dispatch = useDispatch()

    const maximize = (item) => {
        dispatch(maximizeWindow(item))
    }

    return(
        <div style={{position: 'fixed', bottom: '0', left: '0', width: '100%', zIndex: 1000, display: 'inline-block'}}>
            <Panel
                variant='outside'
                shadow
                style={{padding: '1rem', lineHeight: '1.5', width: '100%',}}
            >
                {windows.map(window => {
                    if (window.minimized) {
                        return (
                            <Button
                                key={window.id}
                                onClick={() => maximize(window)}
                                variant='menu'
                            >
                                {window.type}
                                <Divider></Divider>
                            </Button>
                        )
                    }
                })}
            </Panel>
        </div>
    )
}

export default Footer
